.media-scroller {
  display: grid;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  grid-auto-flow: column;
  grid-auto-columns: 35%;
  overflow-x: auto;
  overscroll-behavior-inline: contain;
}

.media-element {
  display: grid;
  grid-template-rows: -webkit-min-content;
  grid-template-rows: min-content;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  padding: 0.5rem;
}


/* PAGINATION KNEEL SLIDER CSS */

.dots {
  display: flex;
  padding: 10px 0;
  justify-content: center;
}

.dot {
  border: none;
  width: 10px;
  height: 10px;
  background: #BABDC9;
  border-radius: 50%;
  margin: 0 5px;
  padding: 5px;
  cursor: pointer;
}

.dot:focus {
  outline: none;
}

.dot.active {
  background: #1C9B51;
  border-radius: 28px;
  width: 35px;
  padding: 0 5px;
  height: 10px;
}
.navigation-wrapper {
